import { Component } from '@angular/core';
import { AuthService }from './services/auth.service';
import { environment }from '../environments/environment';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { switchMap } from 'rxjs/operators';
import { Subject, Observable, of, throwError  } from 'rxjs';
import 'rxjs/add/operator/do';

interface userCache{
  TOKEN: "",
  user:{
    _id:"",
    name: "",
    lastName: "",
    phone: 0,
    email:"",
    role:""
  },
  isLog: false
}

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'client';
  pass;
  email;
  isLog;
  tipo;
  private cache: Map<string, userCache> = new Map<string, userCache>();
  private inFlightObservables: Map<string, Subject<any>> = new Map<string, Subject<any>>();


  constructor(public authS: AuthService, private route: ActivatedRoute,
  private router: Router){

    console.log(`has: ${this.has('user')} in cache: ${this.getC('user')}`);
    if(this.has('user')) {
      let cache:any = this.getC('user');
    console.log(`cache: ${this.has('cache')}`);


    }

    this.isLog = environment.isLog

  }

  logIn(){
  	this.authS.signIn(this.pass, this.email).then(resolve =>{
  		let rs:any = resolve;
  		console.log(rs);
      environment.isLog = rs.ok;
      this.isLog = rs.ok;
  		if(rs.ok) {
        this.setC('user',{ TOKEN: rs.token,
            user: rs.user, isLog: rs.ok });
        environment.TOKEN = rs.token;
        environment.user = rs.user;
        this.tipo = rs.user.role;
        if(rs.user.role == 'ADMIN') {
        this.router.navigate(['/users']);
  
        }else if(rs.user.role == 'MEDIC'){
        this.router.navigate(['/hist']);

        }
  		}
  	})
  }

  setC(key: string, data: userCache): void {
    this.cache.set(key, data);
    this.notifyInFlightObservers(key, data);
  }

  private notifyInFlightObservers(key: string, value: any): void {
    if (this.inFlightObservables.has(key)) {
      const inFlight = this.inFlightObservables.get(key);
      const observersCount = inFlight.observers.length;
      if (observersCount) {
        console.log(`%cNotifying ${inFlight.observers.length} flight subscribers for ${key}`, 'color: blue');
        inFlight.next(value);
      }
      inFlight.complete();
      this.inFlightObservables.delete(key);
    }
  }

   getC(key: string, fallback?: Observable<any>, maxAge?: number): Observable<any> | Subject<any> {

    if (this.has(key)) {
      console.log(`%cGetting from cache ${key}`, 'color: green');
      return of(this.cache.get(key));
    }

    if (this.inFlightObservables.has(key)) {
      return this.inFlightObservables.get(key);
    } else if (fallback && fallback instanceof Observable) {
      this.inFlightObservables.set(key, new Subject());
      console.log(`%c Calling api for ${key}`, 'color: purple');
      return fallback.do((value) => { this.setC(key, value); });
    } else {
      console.log("no exite", 'color: red');
      return throwError('Requested key is not available in Cache');
    }

  }

  has(key: string): boolean {
    return this.cache.has(key);
  }
}