import { HttpClient, HttpHeaders, HttpErrorResponse } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment }from '../../environments/environment';

@Injectable({
  providedIn: 'root'
})
export class CitesService {

	urlApi:string="https://api-movil.herokuapp.com/api/cite";
    urlLocal = "http://localHost:3000/api/cite";
    url= this.urlApi;
    httpOptions = {
        headers: new HttpHeaders({'authorization': `Bearer ${environment.TOKEN}` ,'Content-Type':  'application/json','Accept':'application/json'})
      };


  constructor(public http: HttpClient) { }

  	getCites(){
  		return new Promise(resolve =>{
  			this.http.get(`${this.url}`, this.httpOptions)
	           .subscribe(res=>{
	             resolve(res);
            })
  		})
  	}

  	getCiteMed(id){
  		return new Promise(resolve =>{
  			this.http.get(`${this.url}/medic/${id}`, this.httpOptions)
	           .subscribe(res=>{
	             resolve(res);
            })
  		})
  	}

  	deleteCite(id: string){
	    console.log('nnn')
	   // let token =this.auth.getToken();
	    //const httpOptions = {
	      //headers: new HttpHeaders({'Authorization': `Bearer ${token.token}`,'Content-Type':  'application/json','Accept':'application/json'})};
	    return new Promise(resolve =>{
	      this.http.delete(`${this.url}/${id}`, this.httpOptions)
	         .subscribe((res =>{
	           console.log("eliminada")
	           console.log(res);
	           resolve(res)
	         }));
	    })
    }
}
