import { Component, OnInit } from '@angular/core';
import { User, types }from '../../models/user';
import { UserService }from '../../services/user.service';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { switchMap } from 'rxjs/operators';

@Component({
  selector: 'app-new-user',
  templateUrl: './new-user.component.html',
  styleUrls: ['./new-user.component.css']
})
export class NewUserComponent implements OnInit {

	user : User={
		name: "",
	    lastName: "",
	    email: "",
	    doc: "",
	    password: "",
	    role: types.USER,
	    phone:0,
	};

  name;
  lastName;
  email;
  doc;
  password;
  role:types =  types.USER;
  phone= 0;

	tipo;
  horario;

  constructor(public userS: UserService,  private route: ActivatedRoute,
  private router: Router) { }

  ngOnInit() {
  }

  registrar(){
    let horarios=[];
  	this.password = `${this.doc}`;
  	if(this.tipo == "usuario"){
  		this.role = types.USER
  	}else if(this.tipo == "medico"){
  		this.role = types.MEDIC;
      this.horario.split(',').forEach((item, index)=>{
        horarios.push({fecha:item.split(' ')[0],hora:item.split(' ')[1]});
        console.log(horarios[index]);
      });
  	}

    let user = {
      name: this.name,
      lastName:this.lastName,
      email: this.email,
      doc: this.doc,
      password: this.password,
      role: this.role,
      phone: this.phone,
      horarios: horarios
    }

  	this.userS.postUser(user).then(resolve =>{
  	  	this.router.navigate(['users']);
  	})
  }

}
