import { Component, OnInit } from '@angular/core';
import { CitesService }from '../../services/cites.service';
import { HistoService }from '../../services/histo.service';
import { environment }from '../../../environments/environment';
@Component({
  selector: 'app-histor',
  templateUrl: './histor.component.html',
  styleUrls: ['./histor.component.css']
})
export class HistorComponent implements OnInit {

  constructor(public citeS:CitesService,public h:HistoService) { }

usuarios=[];
citas;

usuario;
receta;
historial;
  ngOnInit() {
  	this.citeS.getCiteMed(environment.user._id).then(resolve=>{
  		let rs:any = resolve;
  		this.citas = rs.cites;
  		console.log(rs.cites[0])
  		this.citas.forEach((item,index)=>{
  			this.usuarios.push(item.pacient)
  		})
  	})
  }


  registrar(){
  	console.log(this.usuario.name)
  	this.h.addDatasCite({historia:this.historial,medic:{name:environment.user.name,_id:environment.user._id},
  	 pacient:{name:this.usuario.split(' ')[0],_id:this.usuario.split(' ')[1]}},
  	 {receta:this.receta,medic:{name:environment.user.name,_id:environment.user._id},
  	 pacient:{name:this.usuario.split(' ')[0],_id:this.usuario.split(' ')[1]}}).then(resolve=>{
  	 	this.historial = "";
  	 	this.receta = "";
  	 	this.usuario = "";
  	})
  }



}
