import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment }from '../../environments/environment';

@Injectable({
  providedIn: 'root'
})
export class HistoService {

  constructor(public http: HttpClient) { }
  

  	urlApi:string="https://api-movil.herokuapp.com/api";
    urlLocal = "http://localHost:3000/api";
    url= this.urlApi;
    httpOptions = {
        headers: new HttpHeaders({'authorization': `Bearer ${environment.TOKEN}` ,'Content-Type':  'application/json','Accept':'application/json'})
      };


  	getHistory(){
  		return new Promise(resolve =>{
  			this.http.get(`${this.url}`, this.httpOptions)
	           .subscribe(res=>{
	             resolve(res);
            })
  		})
  	}

    addDatasCite(histo, receta){
      return new Promise(resolve=>{
        this.http.post(`${this.url}/history`,histo, this.httpOptions)
             .subscribe(res=>{
                 this.http.post(`${this.url}/recipe`,receta, this.httpOptions)
             .subscribe(res=>{
               resolve(res)
            })
            })
      })
    }

}
