import { Component, OnInit } from '@angular/core';
import { UserService }from '../../services/user.service';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { switchMap } from 'rxjs/operators';

@Component({
  selector: 'app-users',
  templateUrl: './users.component.html',
  styleUrls: ['./users.component.css']
})
export class UsersComponent implements OnInit {

  constructor(public userS: UserService, private route: ActivatedRoute,
  private router: Router,) { }

  users;
  user;

  ngOnInit() {
  	this.getUsers();
  }

  getUsers(){
    this.userS.getUsers().then(resolve=>{
      this.users = resolve;
    })
  }

  deleteUser(user) {
    if(confirm(`Desea eliminar al usuario con Documento # ${user.doc}`)){
      this.userS.deleteUser(user._id).then(resolve =>{
        console.log('sasasas')
        this.getUsers();
    });  
    }
  }

  updateUser(user){
        this.router.navigate([`user/${user.doc}`]);
  }

}
