import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { HttpClientModule } from '@angular/common/http';
import { AppComponent } from './app.component';
import { UsersComponent } from './components/users/users.component';
import { NewUserComponent } from './components/new-user/new-user.component';
import { FormsModule } from '@angular/forms';
import { RouterModule, Routes } from '@angular/router';

import { UserService }from './services/user.service';
import { AuthService }from './services/auth.service';
import { MainComponent } from './components/main/main.component';
import { CitesComponent } from './components/cites/cites.component';
import { HistorComponent } from './component/histor/histor.component';
import { NewHistoComponent } from './components/new-histo/new-histo.component';

const appRoutes: Routes = [
  { path: 'users', component: UsersComponent },
  { path: 'user/:id',      component: UsersComponent },
  { path: '',      redirectTo: 'dashboard', pathMatch: 'full' },
  { path: 'dashboard',      component: AppComponent },
  { path: 'cites',      component: CitesComponent },
  { path: 'newU',      component: NewUserComponent },
  { path: 'hist',      component: HistorComponent },
  { path: 'newHist',      component: NewHistoComponent },
];

@NgModule({
  declarations: [
    AppComponent,
    UsersComponent,
    NewUserComponent,
    MainComponent,
    CitesComponent,
    HistorComponent,
    NewHistoComponent
  ],
  imports: [
    BrowserModule,
     HttpClientModule,
     FormsModule,
     RouterModule.forRoot(
      appRoutes,
      { enableTracing: false } // <-- debugging purposes only
    )
  ],
  providers: [
      UserService,
      AuthService,
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
