import { Component, OnInit } from '@angular/core';
import { CitesService }from '../../services/cites.service';

@Component({
  selector: 'app-cites',
  templateUrl: './cites.component.html',
  styleUrls: ['./cites.component.css']
})
export class CitesComponent implements OnInit {

	cites;

  constructor(public citesS:CitesService) { }

  ngOnInit() {
  	this.getCites();
  }

  getCites(){
  	this.citesS.getCites().then(resolve =>{
  		this.cites = resolve;
      console.log(this.cites[0])
  	})
  }

    deleteCite(cite) {
    if(confirm(`Desea eliminar la cita`)){
      this.citesS.deleteCite(cite._id).then(resolve =>{
        this.getCites();
    });  
    }
  }

}
